/* eslint-disable import/first */
import axios from 'axios';
const { Kakao } = window;

export class AuthService {
    constructor(
        private kakaoLoginRestApiKey,
        private kakaoLoginSdkKey,
        private redirectUri
    ) {
        Kakao.init(this.kakaoLoginSdkKey);
    }

    getAuthorizationUrl = () => {
        return `https://kauth.kakao.com/oauth/authorize?client_id=${this.kakaoLoginRestApiKey}&redirect_uri=${this.redirectUri}&response_type=code`;
    };

    //  
    //   ( ,  )  redirectUri  
    //   -> Home  + Kakao.API.request    state 
    //    -> Login 
    getAuthorizationToken = async (code, redirectUri) => {
        const grant_type = 'authorization_code';
        const data = {
            grant_type,
            client_id: this.kakaoLoginRestApiKey,
            redirect_uri: redirectUri || this.redirectUri,
            code,
        };
        const queryString = Object.keys(data)
            .map(
                (k) => encodeURIComponent(k) + '=' + encodeURIComponent(data[k])
            )
            .join('&');
        try {
            const token = await axios.post(
                'https://kauth.kakao.com/oauth/token',
                queryString,
                {
                    headers: {
                        'Content-type':
                            'application/x-www-form-urlencoded;charset=utf-8',
                    },
                }
            );
            return token.data.access_token;
        } catch (e: any) {
            throw new Error(e.message);
        }
    };

    /**
     *
     * @param token getAuthorizationToken  
     * @param getUserInfo     
     * @param errorCallback     
     */
    login = async (token, getUserInfo, errorCallback) => {
        try {
            Kakao.Auth.setAccessToken(token);
            Kakao.API.request({
                url: '/v2/user/me',
                success: getUserInfo,
                fail:
                    errorCallback ||
                    ((error) => {
                        //   redirect
                        console.log(error);
                    }),
            });
        } catch (e: any) {
            throw new Error(e.message);
        }
    };

    logout = async (handleRedirect) => {
        if (!Kakao.Auth.getAccessToken()) {
            console.log('  .');
            return;
        }

        try {
            Kakao.Auth.logout(handleRedirect);
        } catch (e: any) {
            throw new Error(e.message);
        }
    };
}
